import { loadData } from '@/api/common'
import Scroll from '@/components/scroll'
export default {
  components: {
    Scroll
  },
  data() {
    return {
      countdown: true,
      count: '',
      timer: null,
      forGetInfo: {
        smsCode: '',
        phone: '',
        password: ''
      }
    }
  },
  methods: {
    login() {
      loadData('/login/reset_pass', this.forGetInfo).then(data => {
        this.$router.push('/memberCenter')
      })
    },
    getCode() {
      const TIME_COUNT = 60
      if (!this.timer) {
        this.count = TIME_COUNT
        this.countdown = false
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--
          } else {
            this.countdown = true
            clearInterval(this.timer)
            this.timer = null
          }
        }, 1000)
      }
    }
  }
}



